import React, { useState } from "react";

const ToDonesContainer = ({ Dones, UpdateItem, DeleteItem }) => {
  // const todones = [];

  // const [toDones, setToDones] = useState(todones);

  const toDoNesItems = Dones.map((el) => {
    return (
      <div className="todones-item" key={el.text}>
        <p>{el.text}</p>
        <div className="actions">
          <button
            className="btn"
            onClick={() => {
              UpdateItem(el.id);
            }}
          >
            &#8635;
          </button>
          <button
            className="btn"
            onClick={() => {
              DeleteItem(el.id);
            }}
          >
            x
          </button>
        </div>
      </div>
    );
  });

  return (
    <div className="todones-container">
      <h3>BACKLOG</h3>
      {Dones.length > 0 && toDoNesItems}
    </div>
  );
};

export default ToDonesContainer;
