import React, { useState, useEffect } from "react";
import "../css/App.scss";
import Navigation from "./Navigation";
import ToDoContainer from "./ToDoContainer";
import ToDonesContainer from "./ToDonesContainer";
import { v4 as uuid } from "uuid";
import { HashRouter, Route, Switch } from "react-router-dom";
import Help from "../views/Help";
import PageNotFound from "../views/PageNotFound";

const App = () => {
  const [tasks, setTasks] = useState([
    // { id: uuid(), text: "Make a website", done: true },
    // { id: uuid(), text: "Wash my face!", done: false },
    // { id: uuid(), text: "Call my mom", done: true },
  ]);

  const ToDos = tasks.filter((task) => !task.done);
  const Dones = tasks.filter((task) => task.done);

  const AddItem = (text) => {
    let task = { id: uuid(), text: text, done: false };
    localStorage.setItem("ToDoItems", JSON.stringify([...tasks, task]));
    setTasks([...tasks, task]);
  };

  useEffect(() => {
    // component did mount
    let data = localStorage.getItem("ToDoItems");

    if (data) {
      let parsedData = JSON.parse(data);
      setTasks(parsedData);
    }
  }, []);

  const UpdateItem = (id) => {
    let updatedTasks = tasks.map((task) => {
      if (task.id === id) {
        task.done = !task.done;
        return task;
      }
      return task;
    });
    localStorage.setItem("ToDoItems", JSON.stringify(updatedTasks));
    setTasks(updatedTasks);
  };

  const DeleteItem = (id) => {
    localStorage.setItem(
      "ToDoItems",
      JSON.stringify((task) => task.id !== id)
    );
    setTasks(tasks.filter((task) => task.id !== id));
  };

  return (
    <HashRouter>
      <div className="app">
        <Navigation></Navigation>

        <Switch>
          <Route exact path="/">
            <ToDoContainer
              AddItem={AddItem}
              ToDos={ToDos}
              UpdateItem={UpdateItem}
            ></ToDoContainer>
            <ToDonesContainer
              Dones={Dones}
              UpdateItem={UpdateItem}
              DeleteItem={DeleteItem}
            ></ToDonesContainer>
          </Route>
          <Route path="/help">
            <Help />
          </Route>
          <Route>
            <PageNotFound />
          </Route>
        </Switch>
      </div>
    </HashRouter>
  );
};

export default App;
