import React, { useRef } from "react";

const ToDoContainer = ({ ToDos, AddItem, UpdateItem }) => {
  // const todos = [];

  const input = useRef();

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(input.current.value);
    AddItem(input.current.value);
    // input.current.value = "";  or you can do:
    e.target.reset();
  };

  const toDoItems = ToDos.map((el) => {
    return (
      <div className="todo-item" key={el.text}>
        <p>{el.text}</p>
        <div className="actions">
          <button className="btn" onClick={() => UpdateItem(el.id)}>
            &#10004;
          </button>
        </div>
      </div>
    );
  });

  return (
    <div className="todos-container">
      <form className="todo-form" onSubmit={handleSubmit}>
        <label className="input-item">
          <input type="text" name="todo" ref={input} />
        </label>
        <input className="btn" type="submit" value="ADD" />
      </form>
      <div className="todos">
        <h3>TO DO</h3>
        {ToDos.length > 0 && toDoItems}
      </div>
    </div>
  );
};

export default ToDoContainer;
